import React from "react";
import "./Card.scss";
import { BsHandbagFill } from "react-icons/bs";
import { Link } from "react-router-dom";

const Card = ({ type }) => {
  return (
    <div className={"card " + type}>
      <Link className="links" to="/eyemax-store/san-pham/1">
        <div className="card-title">
          <h3>Tên sản phẩm</h3>
        </div>
        <div className="card-images">
          <img
            src="https://www.chanchao.com.tw/VIBS/images/default.jpg"
            alt=""
            className="main-img"
          />
          <img
            src="https://www.chanchao.com.tw/VIBS/images/default.jpg"
            alt=""
            className="second-img"
          />
        </div>

        <div className="card-price">
          <span>
            {new Intl.NumberFormat("vi-VN", {
              style: "currency",
              currency: "VND",
            }).format(0)}
          </span>
        </div>
      </Link>
      <div className="card-widget">
        <span className="card-widget-button">
          <BsHandbagFill />
        </span>
      </div>
    </div>
  );
};

export default Card;
