import React, { useEffect, useState } from "react";
import "./Products.scss";
import Card from "../../components/Card/Card";
import Breadcrumb from "../../components/Breadcrumb/Breadcrumb";

const item = [
  {
    name: "Trang chủ",
    link: "/eyemax-store",
  },
  {
    name: "Sản phẩm",
    link: "/eyemax-store/san-phams",
  },
];

const Products = () => {
  return (
    <>
      <Breadcrumb item={item} />
      <div className="products">
        <div className="products-left col-9">
          <div className="products-header">
            <div className="products-sort">
              <label>Sắp xếp</label>
              <select>
                <option id="asc" value="asc" name="asc">
                  Mặc định
                </option>
                <option id="asc" value="asc" name="asc">
                  Sắp xếp theo tên (A-Z)
                </option>
                <option id="desc" value="desc" name="desc">
                  Sắp xếp theo tên (Z-A)
                </option>
                <option value="3">{"Sắp xếp theo giá (Nhỏ -> Lớn)"}</option>
                <option value="4">{"Sắp xếp theo giá (Lớn -> Nhỏ)"}</option>
              </select>
            </div>
          </div>
          <div className="products-box">
            <Card type="card" />
            <Card type="card" />
            <Card type="card" />
            <Card type="card" />
            <Card type="card" />
            <Card type="card" />
            <Card type="card" />
            <Card type="card" />
            <Card type="card" />
          </div>

          <div className="products-pagging">
            <nav aria-label="Page navigation example">
              <ul className="pagination">
                <li className="page-item active">
                  <div className="page-link">1</div>
                </li>
                <li className="page-item ">
                  <div className="page-link">2</div>
                </li>
                <li className="page-item ">
                  <div className="page-link">3</div>
                </li>
              </ul>
            </nav>
          </div>
        </div>
        <div className="products-right col-3">
          <div className="products-filter">
            <h2>TÌM KIẾM</h2>
            <div className="products-form">
              <input
                type="text"
                placeholder="Nhập từ khoá..."
                // onChange={(e) => setQuery(e.target.value.toLowerCase())}
              />
            </div>
          </div>

          <div className="products-filter">
            <h2>Chất liệu</h2>

            <div className="products-input">
              <input type="checkbox" />
              <label>Chất liệu 1</label>
            </div>
            <div className="products-input">
              <input type="checkbox" />
              <label>Chất liệu 2</label>
            </div>
            <div className="products-input">
              <input type="checkbox" />
              <label>Chất liệu 3</label>
            </div>
          </div>

          <div className="products-filter">
            <h2>Hình dáng</h2>

            <div className="products-input">
              <input type="checkbox" />
              <label>Hình dáng 1</label>
            </div>
            <div className="products-input">
              <input type="checkbox" />
              <label>Hình dáng 2</label>
            </div>
            <div className="products-input">
              <input type="checkbox" />
              <label>Hình dáng 3</label>
            </div>
          </div>

          <div className="products-filter">
            <h2>Danh mục</h2>

            <div className="products-input">
              <input type="checkbox" v />
              <label>Danh mục 1</label>
            </div>
            <div className="products-input">
              <input type="checkbox" v />
              <label>Danh mục 2</label>
            </div>
            <div className="products-input">
              <input type="checkbox" v />
              <label>Danh mục 3</label>
            </div>
          </div>

          <div className="products-filter">
            <h2>Khoảng giá</h2>
            <div className="products-input">
              <input type="checkbox" value="100-200" id="100-200" />
              <label htmlFor="100-200">100.000đ — 200.000đ</label>
            </div>
            <div className="products-input">
              <input type="checkbox" value="200-300" id="200-300" />
              <label value="200-300" htmlFor="200-300">
                200.000đ — 300.000đ
              </label>
            </div>
            <div className="products-input">
              <input type="checkbox" value="300-500" id="300-500" />
              <label htmlFor="300-500">300.000đ — 500.000đ</label>
            </div>
            <div className="products-input">
              <input type="checkbox" value="500-1000" id="500-1000" />
              <label htmlFor="500-1000">500.000đ — 10.000.000đ</label>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Products;
