import React from "react";
import "./Cart.scss";
import Breadcrumb from "../../components/Breadcrumb/Breadcrumb";
import { Link } from "react-router-dom";
import CartItem from "../../components/CartItem/CartItem";

const item = [
  {
    name: "Trang chủ",
    link: "/eyemax-store",
  },
  {
    name: "Giỏ hàng",
    link: "/eyemax-store/gio-hang",
  },
];
const Cart = () => {
  return (
    <>
      <Breadcrumb item={item} />
      <div className="cart">
        <div className="cart-top">
          <h2>Giỏ hàng</h2>
        </div>
        <div className="cart-center">
          <CartItem type="cart-item" item={item} key={item.MaSanPham} />
        </div>
        <div className="cart-bottom">
          <div className="cart-total">
            <p>Tổng tiền:</p>
            <span>
              {new Intl.NumberFormat("vi-VN", {
                style: "currency",
                currency: "VND",
              }).format(0)}
            </span>
          </div>
          <div className="cart-button">
            <Link className="links cart-more" to="/eyemax-store/san-phams">
              Xem thêm sản phẩm
            </Link>

            <Link className="links cart-pay" to="/eyemax-store/thanh-toan">
              Thanh toán
            </Link>
          </div>
        </div>
      </div>
    </>
  );
};

export default Cart;
